<template>
  <v-container fluid class="fill-height pa-0" id="container">
    <v-row id="row" class="fill-height" justify="center">
      <v-col class="text-center pa-0">
        <div id="map" style="stroke: #dcdcdc; stroke-width: 2px; display: flex;">
        </div>
      </v-col>
    </v-row>
    <v-card style="position: absolute; top: 1%; left: 50%; transform: translate(-50%, 0%)">
      <div class="d-flex">
        <div v-for="(party, index) in data.parties" :key="index" class="pa-2" v-ripple @click="select(party)" :style="{ backgroundColor: hsl(party.color) }">
          <v-img :src="require(`@/assets/logos/${party.logo}`)" height="70px" width="300px" contain></v-img>
        </div>
      </div>
      <div class="d-flex" style="background-color: #808080;">
        <template v-for="(party, index) in data.parties">
          <div v-if="count.get(party) > 0" :key="index" class="pa-1 text-center" @click="select(party)" :style="{ backgroundColor: hsl(party.color), width: progressBar.get(party) }" style="transition:width 0.5s;">
            <h3 class="font-weight-black white--text" style="white-space: nowrap; text-overflow: ellipsis; overflow: hidden;">
              {{ count.get(party) }}
              <br/>
              ({{ Math.round((count.get(party) / total * 100) * 10) / 10 }}%)
            </h3>
          </div>
        </template>
      </div>
    </v-card>
  </v-container>
</template>

<script>
import * as d3 from 'd3'

export default {
  name: 'Election',

  props: ['data'],

  data: function () {
    const count = new Map()
    for (const party of this.data.parties) {
      count.set(party, 0)
    }
    return {
      selected: this.data.parties[0],
      count: count,
      total: 0,
      provinces: new Map()
    }
  },

  computed: {
    progressBar: function () {
      const ratio = new Map()
      for (const [key, value] of this.count) {
        ratio.set(key, `${(value / this.total) * 100}%`)
      }
      return ratio
    }
  },

  watch: {
    data: function () {
      this.draw()
    }
  },

  mounted: function () {
    this.draw()
  },

  methods: {
    select (party) {
      if (party !== this.selected) {
        this.selected = party
      }
    },
    hsl (arr) {
      const h = arr[0]
      const s = arr[1]
      const l = arr[2]
      return `hsl(${h}, ${s}%, ${l}%)`
    },
    draw () {
      const map = require(`@/assets/elections/${this.data.map}`)

      const height = document.getElementById('container').clientHeight
      const width = document.getElementById('row').clientWidth

      document.getElementById('map').replaceChildren()
      const svg = d3.select('#map').append('svg').attr('width', width).attr('height', height)
      const projection = d3.geoMercator().translate([0, 0]).scale(1)
      const path = d3.geoPath().projection(projection)
      const bounds = path.bounds(map)
      const widthScale = (bounds[1][0] - bounds[0][0]) / width
      const heightScale = (bounds[1][1] - bounds[0][1]) / height
      const scale = 0.85 / Math.max(widthScale, heightScale)
      const xoffset = width / 2 - scale * (bounds[1][0] + bounds[0][0]) / 2 + 10
      const yoffset = height / 2 - scale * (bounds[1][1] + bounds[0][1]) / 2 + 80
      const offset = [xoffset, yoffset]
      projection.scale(scale).translate(offset)

      const _this = this

      function click (d) {
        let level = 0
        if (!_this.provinces.has(this)) {
          const count = _this.count.get(_this.selected)
          _this.count = new Map(_this.count.set(_this.selected, count + 1))
        } else {
          if (_this.provinces.get(this).party !== _this.selected) {
            const prevParty = _this.provinces.get(this).party
            let count = _this.count.get(_this.selected)
            _this.count.set(_this.selected, count + 1)
            count = _this.count.get(prevParty)
            _this.count = new Map(_this.count.set(prevParty, count - 1))
          }
          if (_this.provinces.get(this).party === _this.selected && _this.provinces.get(this).level < 3) {
            level = _this.provinces.get(this).level + 1
          }
        }
        _this.provinces.set(this, { party: _this.selected, level: level })
        const s = _this.selected.color[1] - (10 * level)
        const l = _this.selected.color[2] + (10 * level)
        const color = _this.hsl([_this.selected.color[0], s, l])
        d3.select(this).transition().duration(500).style('fill', color)
      }

      const g = svg.append('g')
      g.selectAll('path')
        .data(map.features)
        .enter()
        .append('path')
        .attr('d', path)
        .attr('vector-effect', 'non-scaling-stroke')
        .style('fill', '#808080')
        .on('click', click)

      svg.call(d3.zoom().scaleExtent([1, 20]).on('zoom', function (e) {
        g.attr('transform', e.transform)
      })).on('dblclick.zoom', null)

      this.total = document.getElementsByTagName('g')[0].children.length
      for (const party of this.data.parties) {
        this.count.set(party, 0)
      }
    }
  }
}
</script>

<style scoped>
.row {
  margin-top: 0px !important;
  margin-bottom: 0px !important;
}
</style>
